<template>
  <div id="pdf-viewer">
    <div class="center">
      <div class="contor">
        <button @click="prev"></button>
        <button @click="next"></button>
        <span style="margin: 0 10px; color: #fff">
          
          <span v-text="pageNum"></span> /
          <span v-text="pageCount"></span>
        </span>
        <button @click="plus">+</button>
        <button @click="minus">-</button>
        <button @click="closepdf"></button>
      </div>
      <canvas ref="canvas"></canvas>
    </div>
  </div>
</template>

<script>
import PDFJS from "pdfjs-dist";

export default {
  name: "vuePdfViewer",
  props: ["pdfurl"],
  data() {
    return {
      pdfDoc: null, // pdfjs 
      pageRendering: false,  //
      pageNumPending: null,
      scale: 1.2, // 
      pageNum: 1, // 
      pageCount: null, // 
      maxscale: 2, // 
      minscale: 0.8 // 
    };
  },
  methods: {
    renderPage(pageNum) {
      this.pageRendering = true;
      this.pdfDoc.getPage(pageNum).then(page => {
        const viewport = page.getViewport(this.scale);

        const canvas = this.$refs.canvas;
        const context = canvas.getContext("2d");
        canvas.height = viewport.height;
        canvas.width = viewport.width;

        const renderContext = {
          canvasContext: context,
          viewport: viewport
        };
        const renderTask = page.render(renderContext);
        // Wait for rendering to finish
        renderTask.promise.then(() => {
          this.pageRendering = false;
          const pageNumPending = this.pageNumPending;
          if (pageNumPending !== null) {
            // New page rendering is pending
            renderPage(pageNumPending);
            this.pageNumPending = null;
          }
        });
      });
    },
    //
    plus() {
      if (this.scale >= this.maxscale) return;
      this.scale += 0.1;
      this.queueRenderPage(this.pageNum);
    },
    //
    minus() {
      if (this.scale <= this.minscale) return;
      this.scale -= 0.1;
      this.queueRenderPage(this.pageNum);
    },
    //
    prev() {
      if (this.pageNum <= 1) return;
      this.pageNum--;
      this.queueRenderPage(this.pageNum);
    },
    //
    next() {
      if (this.pageNum >= this.pageCount) return;
      this.pageNum++;
      this.queueRenderPage(this.pageNum);
    },
    closepdf() {
      this.$emit("closepdf");
    },
    /**
     * If another page rendering in progress, waits until the rendering is
     * finised. Otherwise, executes rendering immediately.
     */
    queueRenderPage(pageNum) {
      this.pageRendering
        ? (this.pageNumPending = pageNum)
        : this.renderPage(pageNum);
    }
  },
  mounted() {
    if (!this.pdfurl) return
    PDFJS.getDocument(this.pdfurl).then(pdfDoc => {
      this.pdfDoc = pdfDoc;
      this.pageCount = this.pdfDoc.numPages;
      this.renderPage(this.pageNum);
    });
  }
};
</script>

<style lang="scss" scoped>
#pdf-viewer {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
  ::-webkit-scrollbar {
    width: 0
  }
  .center {
    text-align: center;
    height: 100%;
    overflow: auto;
    padding-top: 20px;
    .contor {
      margin-bottom: 10px;
    }
  }
}
</style>



